body {
  margin: 0;
}
.game {
  width: 150px;
  margin: 0 auto;
  text-align: center;
}
.game button {
  cursor: pointer;
}
.board {
  display: flex;
  flex-wrap: wrap;
  width: 150px;
  margin: 30px 0;
}
.square {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}
.square {
  cursor: no-drop;
}
.square.empty {
  cursor: pointer;
}
.square div {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.square div.black {
  background-color: black;
}
.square div.red {
  background-color: red;
}
.win {
  border: 2px solid;
  box-sizing: border-box;
  border-radius: 5px;
  color: #999;
}
.red {
  color: red;
}
.black {
  color: black;
}
